.hero-section .hero-list {
  padding: 0;
  display: flex;
  flex-wrap: wrap;
}

.hero-section .hero-list li {
  padding: 0;
  list-style: none;
}

.hero-section .search-control {
  display: flex;
  flex-direction: row;
}

.hero-section .search-control input.search {
  flex: 1 0 0;
  font-size: 1.5rem;
  padding: 0.5em;
  border-top-left-radius: 0.25em;
  border-bottom-left-radius: 0.25em;
  border: 1px solid rgba(0, 0, 0, 0.25);
  outline: none;
  min-width: 0;
}

.hero-section .search-control input.search:focus-within {
  border: 1px solid rgba(0, 0, 0, 0.5);
}

.hero-section .search-control button.search-button {
  font-size: 1.5rem;
  padding: 0.5em;
  border-top-right-radius: 0.25em;
  border-bottom-right-radius: 0.25em;
  border: 1px solid rgba(0, 0, 0, 0.25);
  background-color: var(--primary-color);
  color: white;
}
