.banner-container {
  background-color: var(--primary-color);
}

.banner {
  display: flex;
  align-items: center;
  gap: 1rem;
  color: white;
  height: calc(100vh - var(--header-height));
  margin-top: var(--header-height);
}

.banner h1 {
  font-size: 3rem;
  font-weight: normal;
}

.banner p {
  font-size: 1.5rem;
}

.banner > * {
  flex: 1 0 0;
  min-width: 0;
}

.banner-image {
  max-width: 100%;
  height: auto;
  border-radius: 0.5rem;
}

.hero-buttons {
  display: flex;
  gap: 0.5rem;
}
